
@import url(http://at.alicdn.com/t/font_hnvjgge4ilpojemi.css);
/* reset css */
body,h1,h2,h3,p,ul{ margin: 0; padding: 0;
}
ul{ list-style: none;
}
a{ text-decoration: none; color: #000;
}
.clear:after{ content: ''; display: block; width: 0px; height: 0px;
}
.iconfont{ font-size: 35px;
}
body{
  background-color: #f3f3f3;
  font-family: '微软雅黑', '黑体';
  font-size: 14px;
}
body:after{
  content: '';
  display: block;
  width: 0;
  height: 0;
  clear: both;
}
.main-content{
}
/* css */
header{
  height: 60px;
  background-color: #00ca79;
}
header h1{
  float: left;
  width: 260px;
  line-height: 60px;
  font-size: 20px;
  background-color: #000000;
  color: #00ca79;
  text-align: center;
}
#admin{
  height: 60px;
  line-height: 60px;
  float: right;
  position: relative;
  margin-right: 40px;
}
#admin *{
  float: left;
}
#admin img{
  width: 35px;
  height: 35px;
  margin-top: 10px;
}
#admin p{
  font-size: 16px;
  color: #fff;
}
.icon-arrow-b{
  font-size: 16px;
  color: #fff;
  margin-left: 2px;
}
#admin ul{
  width: 100%;
  height: 40px;
  line-height: 40px;
  position: absolute;
  left: 0px;
  top: 20px;
  background-color: #00ca79;
  transition: .5s top;
}
#admin:hover ul{
  top: 60px;
}
#admin ul:hover{
  top: 60px;
}
#admin ul *{
  width: 100%;
  height: 100%;
}
#admin ul a{
  text-align: center;
  color: #fff;
}
#main-nav{
  float: left;
  width: 260px;
  height: 990px;
  background-color: #000000;
}
#main-nav .iconfont,#main-nav a{
  color: #fff;
}
#main-nav a{
  display: block;
  font-size: 18px;
  /*height: 56px;*/
  line-height: 52px;
  text-indent: 45px;
  position: relative;
}
#main-nav li{
  box-sizing: border-box;
  border-bottom: 1px solid #2f2e31;
  transition: .5s;
}
#main-nav .iconfont{
  position: relative;
  top: 4px;
}
#main-nav .orderform span,#main-nav .distribution span,#main-nav .news span{
  display: inline-block;
  width: 40px;
  height: 26px;
  line-height: 26px;
  text-indent: 0px;
  text-align: center;
  background-color: #00ca79;
  border-radius: 8px;
  position: absolute;
  left: 114px;
  top: 5px;
}
#main-nav .orderform.active span,#main-nav .distribution.active span,#main-nav .news.active span{
  background-color: #000;
  color: #fff;
}
section {
  overflow: hidden;
}
.content{
  margin-top: 80px;
}
footer{
  float: left;
  width: 100%;
  height: 68px;
  line-height: 68px;
  font-size: 20px;
  color: #fff;
  background-color: #00ca79;
  text-indent: 40px;
  font-weight: bold;
}
a.router-link-active{
  background-color: #00ca79;
}
a.router-link-active span{
  background-color: #000!important;
}
#main-nav .active{
  background-color: #00ca79;
}
.head-bar{
  cursor: pointer;
}
.display-none{
  display: none;
}
.display-block{
  display: block!important;
}



